.App {
  height: 100vh;
  width: 100vw;
  background-color: #eee;
  -webkit-transition: background-color 0.3s;
  -o-transition: background-color 0.3s;
  transition: background-color 0.3s;
}

.background {
  height: 78%;
  background-image: url(./images/workhard.jpg);
  background-position: center;
  background-size: cover;
  -webkit-transition: background 0.3s;
  -o-transition: background 0.3s;
  transition: background 0.3s;
}

.todo-wrapper {
  position: absolute;
  bottom: 10px;
  left: 0;
  right: 0;
  width: 70%;
  height: 80%;
  background-color: #fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 0 auto;
  max-width: 780px;
  min-width: 360px;
  min-height: 300px;
  overflow: hidden;
  -webkit-box-shadow: 0 0 20px 1px #d4e7ff;
          box-shadow: 0 0 20px 1px #d4e7ff;
  border-radius: 4px;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}

.todo-header {
  padding: 20px;
  border-bottom: 1px solid #BDBDBD;
  color: #181848;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.todo-header > h1.todo-title {
  -ms-flex-item-align: end;
      align-self: flex-end;
  margin: 60px 16px 0 0;
  position: relative;
}
.todo-header > h1.todo-title img {
  cursor: pointer;
  width: 20px;
  height: 20px;
  position: absolute;
  bottom: 0;
  right: -28px;
}

.todo-list {
  -ms-flex-positive: 1;
      flex-grow: 1;
  height: 0;
  overflow: hidden;
  position: relative;
}
.todo-list > .items-wrapper {
  height: 100%;
  overflow-y: scroll;
  margin-right: -18px;
}
.todo-list > .items-wrapper p {
  color: #ddd;
  font-size: 20px;
  font-weight: bold;
  padding: 20px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.changeTheme {
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
}
@media screen and (max-width: 680px) {
  .changeTheme {
    top: 10px;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    -ms-flex-pack: center;
        justify-content: center;
  }
}
@media screen and (min-width: 681px) {
  .changeTheme {
    left: 10px;
    bottom: 10px;
  }
}


